/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /api/things              ->  index
 */

'use strict';
import fs from 'fs';
import path from 'path';
import config from './../../config/environment';

var ROOT_RESOURCE = path.normalize(config.root + '/server/resources');
var CONSTANT_FILE = path.normalize(ROOT_RESOURCE + '/constant/sorentino.json');

// Gets a list of Things
export function getMenu(req, res) {
  var obj = JSON.parse(fs.readFileSync(CONSTANT_FILE, 'utf8'));
  res.json(obj);
}
